<template>
  <div class="orders">
    <div class="express">
      <div class="give">
        <div>快递配送</div>
        <div @click="onclick">到店自提</div>
      </div>
      <div class="van-cell" is-link @click="showPopup">
        <div v-if="addressInfo==null ">设置收货地址</div>
        <div class="cell" v-else-if="addressInfo!=null">
          <p>
            {{ submitlist && submitlist.addressInfo.real_name }}
            <span>{{ submitlist && submitlist.addressInfo.phone }}</span>
          </p>
          <div>
            <span class="defaults">[默认]</span>
            {{ submitlist && submitlist.addressInfo.province
            }}{{ submitlist && submitlist.addressInfo.city
            }}{{ submitlist && submitlist.addressInfo.district
            }}{{ submitlist && submitlist.addressInfo.detail }}
          </div>
        </div>
        <div class="icon">
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="imgs">
        <img src="../assets/gun.jpg" />
      </div>
      <van-popup v-model="show" position="bottom" closeable>
        <van-address-list
          v-model="chosenAddressId"
          :list="list"
          default-tag-text="默认"
          @add="onAdd"
        />
      </van-popup>
    </div>
    <div class="heot">
      <p>
        共{{
        submitlist && submitlist.cartInfo && submitlist.cartInfo.length
        }}件商品
      </p>
      <div class="he" v-for="k in submitlist && submitlist.cartInfo" :key="k.id">
        <div class="left">
          <img :src="k.productInfo.image" />
        </div>
        <div class="right">
          <span>
            <p>{{ k.productInfo.store_name }}</p>
            <span>×{{ k.cart_num }}</span>
          </span>
          <span>{{ k.productInfo.attrInfo.suk }}</span>
          <span>￥{{ k.productInfo.price }}</span>
        </div>
      </div>
    </div>
    <ul>
      <li @click="showList=true">
        <span>优惠价</span>
        <span>
          请选择
          <van-icon name="arrow" />
        </span>
      </li>
      <li>
        <span>积分抵扣</span>
        <span class="jifen" @click="checkeds">
          当前积分
          <u>{{ submitlist && submitlist.userInfo.integral }}</u>
          <van-checkbox v-model="checked"></van-checkbox>
        </span>
      </li>
      <van-popup
        v-model="showList"
        closeable
        round
        position="bottom"
        :style="{ height: '36%' }"
        class="couponbox"
      >
        <div class="couponTitle">优惠券</div>
        <div class="couponImg">
          <img src="../assets/noCoupon.e524084b.png" />
        </div>
      </van-popup>
      <li>
        <span>会员优惠</span>
        <span>-￥{{ priceGroups && priceGroups.vipPrice }}</span>
      </li>
      <li>
        <span>快递费用</span>
        <span>
          {{
          priceGroups && priceGroups.storePostage == 0
          ? "免运费"
          : priceGroups && "￥" + priceGroups.storePostage
          }}
        </span>
      </li>
      <li>信息备注</li>
      <li>
        <textarea placeholder="请添加备注（150字以内）" />
      </li>
    </ul>
    <div class="Payment">
      <p>支付方式</p>
      <div class="method">
        <div @click="weixin" :class="index == 1 ? 'no' : ''">
          <span>
            <van-icon name="wechat" />微信支付
          </span>
          <span>微信快捷支付</span>
        </div>
        <div @click="yuee" :class="index == 2 ? 'no' : ''">
          <span>
            <van-icon name="gold-coin-o" />余额支付
          </span>
          <span>可用余额：{{ submitlist && submitlist.userInfo.now_money }}</span>
        </div>
      </div>
    </div>
    <div class="Totalprice">
      <div>
        <span>商品总价:</span>
        <span>￥{{ zhifu && zhifu.result.total_price }}</span>
      </div>
      <div v-if="zhifu && zhifu.result.pay_postage">
        <span>运费:</span>
        <span>￥{{ zhifu && zhifu.result.pay_postage }}</span>
      </div>
      <div v-if="zhifu && zhifu.result.deduction_price">
        <span>积分抵扣:</span>
        <span>￥{{ zhifu && zhifu.result.deduction_price }}</span>
      </div>
    </div>
    <div class="total">
      <div>
        <span>
          合计:
          <u>￥{{ zhifu && zhifu.result.pay_price }}</u>
        </span>
        <button @click="settlement">立即结算</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active: 0,
      show: false,
      chosenAddressId: "1",
      list: [
        {
          id: "1",
          name: "张三",
          tel: "13000000000",
          address: "浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室",
          isDefault: true,
        },
      ],
      submitlist: null,
      checked: false,
      priceGroups: null,

      index: 1,
      payType: "weixin",
      useIntegral: 0,
      zhifu: null,
      showList: false,
      addressInfo:[]
    };
  },
  created() {
    this.axios
      .post("http://47.115.51.185/api/order/confirm", {
        cartId: this.$route.params.id,
      })
      .then((data) => {
        this.submitlist = data.data.data;
        this.priceGroups = data.data.data.priceGroup;
        this.addressInfo = data.data.data.addressInfo
        console.log(this.addressInfo)
        console.log("this.submitlist==>", this.submitlist);
        this.locationadd();
      });
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    onclick() {
      this.$toast.setDefaultOptions({ duration: 1000 });
      this.$toast({
        message: "暂无门店信息，您无法选择到店自提",
        icon: "cross",
      });
    },
    onAdd() {
      this.$router.push("/add_address");
    },
    weixin() {
      this.index = 1;
      this.payType = "weixin";
      this.locationadd();
    },
    yuee() {
      this.index = 2;
      this.payType = "yue";
      this.locationadd();
    },
    checkeds() {
      this.checked = !this.checked;
      if (this.checked) {
        this.useIntegral = 1;
      } else {
        this.useIntegral = 0;
      }
      this.locationadd();
    },
    locationadd() {
      this.axios
        .post(
          "http://47.115.51.185/api/order/computed/" + this.submitlist.orderKey,
          {
            addressId: this.submitlist.addressInfo.id,
            couponId: 0,
            payType: this.payType,
            shipping_type: 1,
            useIntegral: this.useIntegral,
          }
        )
        .then((data) => {
          this.zhifu = data.data.data;
          
          console.log("this.zhifu==>",this.zhifu)
        });
    },
    settlement() {
      this.axios
        .post(
          "http://47.115.51.185/api/order/create/" + this.submitlist.orderKey,
          {
            addressId: this.submitlist.addressInfo.id,
            bargainId: 0,
            combinationId: 0,
            couponId: 0,
            from: "weixinh5",
            mark: "",
            payType: this.payType,
            phone: "",
            pinkId: 0,
            real_name: "",
            seckill_id: 0,
            shipping_type: 1,
            store_id: 0,
            useIntegral: this.useIntegral,
          }
        )
        .then((data) => {
          console.log( "this.submitlist==>",this.submitlist)
          console.log("data提交",data);
          this.$toast.loading({
            message: "订单生成中",
            forbidClick: true,
            loadingType: "spinner",
          });
          if (data.data.msg == "微信支付错误返回：mch_id参数格式错误") {
            this.$toast.setDefaultOptions({ duration: 2000 });
            setTimeout(() => {
              this.$toast(data.data.msg);
              this.$router.push(
                `/orders/status/${
                  data.data.data && data.data.data.result.orderId
                }/0?msg=${data.data.msg}`
              );
            }, 1000);
          } else {
            this.$toast.setDefaultOptions({ duration: 2000 });
            setTimeout(() => {
              this.$toast(data.data.msg);
              this.$router.push(
                `/orders/status/${
                  data.data.data && data.data.data.result.orderId
                }/1`
              );
            }, 1000);
          }

          //  this.$router.push(`/orders/submit/${data.data.data.cartId}`);
        });
    },
  },
};
</script>
<style lang="less">
.orders {
  width: 100vw;
  background: #f5f5f5;
  .express {
    width: 92vw;
    padding: 2vw 4vw;
    background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#e93323),
      to(#f5f5f5)
    );
    background-image: linear-gradient(180deg, #e93323 0, #f5f5f5);
    background-image: -moz-linear-gradient(to bottom, #e93323 0, #f5f5f5 100%);
    padding-top: 1rem;
    .give {
      width: 92vw;
      display: flex;
      color: red;
      position: relative;
      div {
        background: white;
        text-align: center;
        font-size: 3.8vw;
        &:first-of-type {
          line-height: 11vw;
          position: relative;
          width: 46vw;
          overflow: hidden;
          left: 0px;
          z-index: 2;
          border-top-right-radius: 10px;
        }
        &:last-of-type {
          line-height: 8vw;
          position: absolute;
          width: 55vw;
          right: 0px;
          bottom: 0px;
          background: #f7c1bd;
          z-index: 1;
        }
      }
    }
    .van-cell {
      width: 92vw;
      padding: 2vw 4vw;
      background: white;
      padding-bottom: 3vw;
      display: flex;
      align-items: center;
      .cell {
        flex: 1;

        p {
          margin: 0px;
          padding: 0px;
          font-size: 4vw;
          span {
            padding-left: 5vw;
          }
        }
        div {
          font-size: 3vw;
          color: #666666;
          .defaults {
            color: red;
            display: inline-block;
            padding-right: 3vw;
          }
        }
      }
      .icon {
        display: flex;
        .van-icon {
          font-size: 5vw;
          color: #797070;
        }
      }
    }
    .imgs {
      background: white;
      img {
        width: 92vw;
        height: 2px;
        display: block;
      }
    }
  }

  .heot {
    width: 100vw;
    margin-bottom: 2vw;
    background: white;
    & > P {
      margin: 0px;
      width: 92vw;
      line-height: 10vw;
      padding: 2vw 4vw;
      border-bottom: 1px solid #eee;
    }
    .he {
      display: flex;
      width: 92vw;
      padding: 2vw 4vw;
      .left {
        width: 17.34vw;
        padding: 2vw 0;
        img {
          width: 17.34vw;
          height: 18vw;
          border-radius: 1vw;
        }
      }
      .right {
        padding-left: 3vw;
        display: flex;
        flex: 1;
        flex-direction: column;
        justify-content: space-evenly;
        span {
          display: flex;
          justify-content: space-between;
          &:first-of-type {
            p {
              overflow: hidden;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
              width: 55vw;
              font-size: 3vw;
              margin: 0px;
            }
            span {
              color: #868686;
              font-size: 4vw;
            }
          }

          &:nth-of-type(2) {
            font-size: 2.6vw;
            color: #868686;
          }
          &:last-of-type {
            font-size: 3vw;
            color: red;
          }
        }
      }
    }
  }
  ul {
    width: 92vw;
    padding: 0px 4vw;
    background: white;
    li {
      display: flex;
      padding: 3vw 0;
      justify-content: space-between;
      justify-items: center;
      border-bottom: 1px solid #eee;
      span {
        color: #35322e;
        &:last-of-type {
          color: #999999;
          font-size: 4vw;
          text-align: right;
        }
      }
      &:nth-of-type(5) {
        border-bottom: none;
        padding-bottom: 1vw;
      }

      &:last-of-type {
        line-height: 3vw;
        font-size: 3.5vw;
        border-bottom: none;
        textarea {
          width: 92vw;
          height: 15vw;
          border: none;
          background: #f9f9f9;
          padding: 3vw;
        }
      }
      &:first-of-type {
        span {
          &:last-of-type {
            display: flex;
            text-align: right;
            align-items: center;
          }
        }
      }
      .jifen {
        display: flex;
        align-items: center;
        u {
          text-decoration: none;
          color: red;
          padding-left: 1vw;
        }
        .van-checkbox {
          width: 6vw;
          margin-left: 3vw;
        }
      }
    }
  }
  .Payment {
    width: 92vw;
    margin-top: 2vw;
    padding: 0vw 4vw;
    background: white;
    P {
      margin: 0px;
      line-height: 10vw;
    }
    .method {
      width: 92vw;
      padding: 2vw 0;
      div {
        display: flex;
        border: 1px solid #eee;
        margin-top: 3vw;
        height: 10vw;
        span {
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;
          font-size: 3.5vw;
          font-weight: 550;
          &:last-of-type {
            font-size: 3vw;
            color: #999;
            font-weight: 400;
            border-left: 1px solid #eee;
          }
          &:first-of-type {
            .van-icon {
              background: #41b035;
              width: 6vw;
              height: 6vw;
              margin-right: 2vw;
              font-size: 4vw;
              line-height: 6vw;
              border-radius: 50%;
              color: white;
            }
          }
        }
        &:last-of-type {
          span {
            &:first-of-type {
              .van-icon {
                background: #fe960f;
                color: white;
              }
            }
          }
        }
      }
      .no {
        border: 1px solid #fc5445;
        span {
          &:first-of-type {
            color: #fc5445;
          }
        }
      }
    }
  }
  .Totalprice {
    width: 92vw;
    padding: 0 4vw;
    margin-top: 3vw;
    background: white;
    margin-bottom: 60px;
    div {
      display: flex;
      width: 92vw;
      justify-content: space-between;
      height: 8vw;
      align-items: center;
      span {
        &:last-of-type {
          font-size: 3.5vw;
          color: #999;
        }
      }
    }
  }
  .total {
    width: 92vw;
    position: fixed;
    bottom: 0px;
    background: white;
    padding: 2vw 4vw;
    div {
      display: flex;
      justify-content: space-between;
      align-items: center;
      button {
        border: none;
        background: #e93323;
        color: white;
        width: 32vw;
        padding: 2vw;
        border-radius: 99px;
        font-size: 4vw;
      }
      span {
        u {
          text-decoration: none;
          color: #fc4141;
        }
      }
    }
  }
}
.van-toast {
  width: 80vw;
}
.couponbox {
  background: #f5f5f5;
  .couponTitle {
    text-align: center;
    padding-top: 5vw;
    font-size: 5vw;
    font-weight: 900;
  }
  .couponImg {
    img {
      margin-left: 15vw;
      width: 70vw;
      height: 50vw;
    }
  }
}
</style>